<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row mt-2 ml-1 mr-1" >            
                <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                    IdentityColumn="user_title_id" :HeaderText="$t('UserTitle')" :ListData="DirectoryData" :HeaderData="Headers"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false" :SortExp="SortExp" :SortBy="SortBy"
                    SearchPosition="top" :SearchFields="topSearchFields"  :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'DETAIL'"> 
                            <em  class="applyjobicon  details" id="ancDetails"  title="" data-toggle-tooltip="tooltip" v-on:click="Detail(data.row.userid)" data-original-title="Details">
                                <i class="fa fa-file-text-o action-icon text-info"></i>
                            </em>
                        </template>
                    </template>
                </tg-list>
            </div>
            <div class="my-popups" v-if="showContactDetail">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">ContactDetail</h5>                     
                            <button v-on:click="CloseContactDetail()" type="button" class="close" data-original-title="Close">
                                <em class="fa fa-times"></em>
                            </button>
                        </div>
                            <div class="border p-3">  
                                <div class="heading-border">
                                    <h5 class="h5">
                                        <span>Communication</span>
                                        <small class="float-right icon_add add cursorpointer" v-on:click="AddCommunication()" id="AddCommunication" title="AddCommunicationService" checkprivilege="yes">
                                                <i class="fa fa-plus pr-2"></i>AddNew
                                        </small>
                                    </h5>                            
                                </div> 
                                <div>
                                <AddCommunication :Userid="Userid" :title="ManageUserTitle" v-if="showCommunication" v-on:close="showCommunication=false" v-on:addReloadData="FetchCommunication();showCommunication=false" v-bind:emitName="'addReloadData'"></AddCommunication>      
                                    <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                                    IdentityColumn="user_title_id" :HeaderText="$t('UserTitle')" :ListData="CommunicationData" :HeaderData="Header1"
                                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords1" :IsSearchFieldsOnTop="false" :SortExp="SortExp" :SortBy="SortBy"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchCommunication"
                                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                                    v-on:PagerButtonClick="pagerMethod1" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata1" :NorecordfoundText="$t('NoRecordfound')">
                                
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'Edit'"> 
                                            <em class="editicon enable editrecord" id="ancEditEmail" v-on:click="EditCommunication(data.row.userid)" title="Edit" >
                                                <i class="fa fa-pencil text-success action-icon"></i>
                                            </em>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'Delete'">                                           
                                            <em class="enable delete" title="Delete" id="ancDelEmail" url="email" v-on:click="DeleteCommunication(data.row.userid)" >
                                                <i class="fa fa-trash text-danger action-icon"></i>
                                            </em>
                                        </template>
                                    </template>
                                </tg-list>
                            </div> 
                        </div>  
                        
                        <div class="border p-3">  
                                <div class="heading-border">
                                    <h5 class="h5">
                                        <span>Address</span>
                                        <small class="float-right icon_add add cursorpointer" v-on:click="AddAddress()" id="AddAddress" title="AddAddressService" checkprivilege="yes">
                                                <i class="fa fa-plus pr-2"></i>AddNew
                                        </small>
                                    </h5>                            
                                </div> 
                                <div>
                                <AddAddress :Userid="Userid" :title="ManageUserTitle" v-if="showAddAddress" v-on:close="showAddAddress=false" v-on:addReloadData="FetchCommunication();showAddAddress=false" v-bind:emitName="'addReloadData'"></AddAddress>      
                                    <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                                    IdentityColumn="user_title_id" :HeaderText="$t('UserTitle')" :ListData="AddressData" :HeaderData="Header2"
                                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords2" :IsSearchFieldsOnTop="false" :SortExp="SortExp" :SortBy="SortBy"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchCommunication"
                                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                                    v-on:PagerButtonClick="pagerMethod1" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata1" :NorecordfoundText="$t('NoRecordfound')">
                                
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'Edit'"> 
                                            <em class="editicon enable editrecord text-center" id="ancEditEmail" v-on:click="EditCommunication(data.row.userid)" title="Edit" >
                                                <i class="fa fa-pencil text-success action-icon"></i>
                                            </em>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'Delete'">                                           
                                            <em class="enable delete text-center" title="Delete" id="ancDelEmail" url="email" v-on:click="DeleteCommunication(data.row.userid)" >
                                                <i class="fa fa-trash text-danger action-icon"></i>
                                            </em>
                                        </template>
                                    </template>
                                </tg-list>
                            </div> 
                        </div>  

                        <div class="border p-3">  
                                <div class="heading-border">
                                    <h5 class="h5">
                                        <span>Contact Number</span>
                                        <small class="float-right icon_add add cursorpointer" v-on:click="AddContactNumber()" id="AddContactNumber" title="AddContactNumberService" checkprivilege="yes">
                                                <i class="fa fa-plus pr-2"></i>AddNew
                                        </small>
                                    </h5>                            
                                </div> 
                                <div>
                                <ContactNumber :Userid="Userid" :title="ManageUserTitle" v-if="showContactNumber" v-on:close="showContactNumber=false" v-on:addReloadData="FetchCommunication();showContactNumber=false" v-bind:emitName="'addReloadData'"></ContactNumber>      
                                    <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                                    IdentityColumn="user_title_id" :HeaderText="$t('UserTitle')" :ListData="ContactNumberData" :HeaderData="Header3"
                                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords3" :IsSearchFieldsOnTop="false" :SortExp="SortExp" :SortBy="SortBy"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchCommunication"
                                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                                    v-on:PagerButtonClick="pagerMethod1" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata1" :NorecordfoundText="$t('NoRecordfound')">
                                
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'Edit'"> 
                                            <em class="editicon enable editrecord text-center" id="ancEditEmail" v-on:click="EditCommunication(data.row.userid)" title="Edit" >
                                                <i class="fa fa-pencil text-success action-icon"></i>
                                            </em>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'Delete'">                                           
                                            <em class="enable delete text-center" title="Delete" id="ancDelEmail" url="email" v-on:click="DeleteCommunication(data.row.userid)" >
                                                <i class="fa fa-trash text-danger action-icon"></i>
                                            </em>
                                        </template>
                                    </template>
                                </tg-list>
                            </div> 
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>
        </div>
        
        <!---modal popup-->

    
</template>
<script>
import UserTitleDataService from "@/services/DataService";
import Schema from "@/views/Company/User/DirectoryListingSchema";
import AddCommunication  from "@/components/Modals/AddCommunication.vue";
import AddAddress  from "@/components/Modals/AddAddress.vue";
import ContactNumber  from "@/components/Modals/ContactNumber.vue";

export default {
    components: {
        AddCommunication,
        AddAddress,
        ContactNumber
    },        
    data() {
        return {
            showContactDetail:false,
            showCommunication:false,
            showAddAddress:false,
            showContactNumber:false,
            Userid:0,
            noRecord:false,        
            isLoading: false,
            CompanyId: null,
            DirectoryData: [],
            CommunicationData: [],
            AddressData: [],
            ContactNumberData: [],
            TotalRecords: 0,
            TotalRecords1: 0,
            TotalRecords2: 0,
            TotalRecords3: 0,
            listheaderbuttons: null,
            topSearchFields: Schema.topsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: null,
            searchCondition: null,
            conditionForInstantSearch: "",
            SortBy: null,
            SortExp: null,
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],            
            MODULENAME: null,
            SUBMODULECODE: null,
            privilegeParams: null,
            sharetype: '',
           
            Headers: [
                {
                    "COLUMN_NAME": "username",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "username",
                    "DISPLAY_ORDER": 1,
                    
                },
                {
                    "COLUMN_NAME": "address",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "address",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "contactnumber",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "contactnumber",
                    "DISPLAY_ORDER": 3,                    
                },
                {
                    "COLUMN_NAME": "DETAIL",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "DETAIL",
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                }, 

            ],
            Header1: [
                {
                    "COLUMN_NAME": "screenName",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "screenName",
                    "DISPLAY_ORDER": 1,
                    
                },
                {
                    "COLUMN_NAME": "communicationType",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "communicationType",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "Edit",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Edit",
                    "DISPLAY_ORDER": 3,
                     settings: {
                        isInSlot: true
                    }                    
                },
                {
                    "COLUMN_NAME": "Delete",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Delete'),
                    "DISPLAY_ORDER": 9,
                    settings: {
                        isInSlot: true
                    }
                },


            ],
            Header2: [
                {
                    "COLUMN_NAME": "address",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "address",
                    "DISPLAY_ORDER": 1,
                    
                },                
                {
                    "COLUMN_NAME": "Edit",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Edit",
                    "DISPLAY_ORDER": 3,
                     settings: {
                        isInSlot: true
                    }                    
                },
                {
                    "COLUMN_NAME": "Delete",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Delete'),
                    "DISPLAY_ORDER": 9,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            Header3: [
                {
                    "COLUMN_NAME": "contact",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "contact",
                    "DISPLAY_ORDER": 1,
                    
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "description",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "Edit",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Edit",
                    "DISPLAY_ORDER": 3,
                     settings: {
                        isInSlot: true
                    }                    
                },
                {
                    "COLUMN_NAME": "Delete",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Delete'),
                    "DISPLAY_ORDER": 9,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            filteredHeaders: [],
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
        }
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    created: function() {
      debugger;
      var vm = this;        
      vm.privilegeParams = `controller=User&action=`;
      vm.FetchData();           
    },       
    methods: {
        renderActions: function (row) {
            console.log(row);
           
        },
        commonHeaderButtonClick: function (event) {
           switch (event.callbackfunction) {
            case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;               
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;                
                case "AddUserTitle":
                    this.showUserTitle = true
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;           
        },        
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            await this.FetchData();
        },    

        Detail: function (userid) {
            debugger;
            var vm = this; 
            vm.userid=userid;
            vm.showContactDetail=true;
            vm.FetchCommunication();          
        },
        CloseContactDetail:function(){
            var vm = this; 
            vm.showContactDetail=false;
        },
        AddCommunication: function () {
            debugger;
            var vm = this;
            vm.showCommunication=true;           
        },
        AddAddress: function () {
            debugger;
            var vm = this;
            vm.showAddAddress=true;           
        },
        AddContactNumber: function () {
            debugger;
            var vm = this;
            vm.showContactNumber=true;           
        },
        FetchData: async function () {
            debugger;
            var vm = this;
            this.isLoading = true;
            //string search, int? pageSize, int? pageNum, string sortBy, string sortExp, long companyId, long loggedInUserId CompanyId=${vueObj.CompanyId}&
            var url=`pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.searchCondition==''?null:vm.searchCondition}`;
            await UserTitleDataService.DirectoryListing(url)
                .then(function (response) {
                    debugger;
                    if(response.data.length>0){
                    vm.DirectoryData = response.data;                    
                    debugger;
                     vm.TotalRecords = vm.DirectoryData[0].total;
                     vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                     vm.CurrentPage = vm.PageNumber;
                     vm.noRecord = false;
                     vm.isLoading=false;
                        
                    }
                    else {
                        vm.noRecord = true;
                        vm.DirectoryData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading=false;
                    }
                    setTimeout(function () {
                        vm.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vm.isLoading = false;
            });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        
        FetchCommunication: async function () {
            debugger;
            var vm = this;
            this.isLoading = true;         
            var url=`UserId=${vm.userid}`;
            await UserTitleDataService.GetContactsofUser(url)
                .then(function (response) {
                    debugger;
                    
                        if(response.data.item1.length>0){
                            vm.CommunicationData=response.data.item1;                                                                          
                            vm.TotalRecords1 = 1;
                        }else{
                            vm.CommunicationData=[]                                                 
                            vm.TotalRecords1 = 0;
                        }
                        if(response.data.item2.length>0){
                            vm.AddressData=response.data.item2;                                                                           
                            vm.TotalRecords2 = 1;
                        }else{
                            vm.AddressData=[];                                               
                            vm.TotalRecords2 = 0;
                        }
                        if(response.data.item3.length>0){
                            vm.ContactNumberData=response.data.item3;                                                                         
                            vm.TotalRecords3 = 1;
                        }else{
                            vm.ContactNumberData=[];                                          
                            vm.TotalRecords3 = 0;
                        }                               
                        setTimeout(function () {                        
                            vm.ResponsiveDataTable('tablelistingdata')
                        }, 500);
                        vm.isLoading = false;
            });
        }, 
        EditCommunication:function(){

        },
        DeleteCommunication:function(){

        },     
    }
}
</script>